@import '~assets/styles/h3Imports';

@mixin tabletUp {
  @media #{$tablet-up} {
    max-width: rem(720);

    .content {
      .info {
        .only-read {
          width: 100%;
        }
      }
    }

    .mobile-button {
      display: none;
    }

    .buttons {
      display: flex;

      * {
        margin-bottom: 0;
        width: rem(164);
      }

      :nth-child(2) {
        margin-left: auto;
        margin-right: rem(16);
      }

      .delete-button {
        border-color: var(--interface-error, #e10900);
        color: var(--interface-error, #e10900);
        margin-left: auto;
        margin-right: rem(16);
      }

      :nth-child(3) {
        margin-right: rem(16);
      }
    }

    .content > .info > .only-read {
      &:first-child > :first-child > :last-child > :first-child {
        margin-right: rem(20);
      }

      &:first-child > :first-child > :last-child > :last-child {
        margin-left: rem(20);
      }
    }
  }
}

@mixin desktopUp {
  @media #{$large-up} {
    max-width: rem(1014);
    padding-left: rem(32);
    padding-right: rem(32);

    .content {
      display: flex;
      flex-direction: row;

      [data-fontsize='AAA'] & {
        flex-direction: column;
      }

      .icon {
        margin-right: 40px;
        max-width: 258px; //rem(258);;
        overflow: hidden;
        width: 100%;

        [data-fontsize='AA'] & {
          margin-right: 0;
        }

        [data-fontsize='AAA'] & {
          margin-bottom: 1rem;
          margin-left: auto;
          margin-right: auto;
        }

        svg {
          height: 158px;
          width: 158px;
        }
      }

      .info {
        width: 100%;

        .only-read {
          &:first-child > :first-child > :first-child {
            margin-top: 0;
          }

          &:first-child > :first-child > :last-child > :first-child {
            margin-right: rem(20);
          }

          &:first-child > :first-child > :last-child > :last-child {
            margin-left: rem(20);
          }
        }
      }
    }
  }
}

@mixin smallOnly {
  @media #{$small-down} {
    .delete-button {
      border-color: var(--interface-error, #e10900);
      color: var(--interface-error, #e10900);
      margin-left: auto;
      margin-right: rem(16);
    }

    .tabletup-button {
      display: none;
    }
  }
}

.wrapper {
  background-color: var(--white);
  border-radius: 8px;
  display: inline-flex;
  flex-direction: column;
  padding: rem(24) 16px;
  width: 100%;

  .buttons {
    padding-top: rem(24);

    * {
      align-items: center;
      display: inline-flex;
      font-size: rem(14);
      font-weight: 700;
      height: rem(40);
      justify-content: center;
      margin-bottom: rem(16);
      padding: 0;
      text-decoration: none;
      width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .card-heading {
    @include h3;

    border-bottom: 1px solid rgba(50, 50, 100, 0.16);
    margin: 0;
    margin-bottom: 32px;
    margin-left: -32px;
    margin-right: -32px;
    padding-bottom: 24px;
    padding-left: 32px;
  }

  .content {
    border-bottom: 1px solid rgba(#323264, 0.16);
    display: inline-flex;
    flex-direction: column;

    .icon {
      display: inline-flex;
      height: 100%;
      justify-content: center;
      margin-right: 0;

      svg {
        display: block;
        height: rem(104);
        width: rem(104);
      }
    }

    .info {
      .only-read {
        border-bottom: 1px solid rgba(#323264, 0.16);
        padding-bottom: rem(8);

        &:first-child > :first-child > :first-child {
          margin-top: rem(40);
        }

        &:first-child > :first-child > :last-child {
          grid-template-columns: 50% 50%;
        }

        &:first-child > :first-child > :last-child > :last-child > *,
        &:first-child > :first-child > :last-child > :first-child > * {
          padding-right: 0;
          width: 100%;
        }

        &:first-child > :first-child > :last-child > :first-child {
          margin-right: 0;
        }

        &:first-child > :first-child > :last-child > :last-child {
          margin-left: 0;
        }
      }
    }
  }

  &.no {
    .icon {
      svg {
        --icon-background: var(--gray-1);
        --icon-stroke: var(--gray-2);
      }
    }
  }

  @include tabletUp;
  @include smallOnly;
  @include desktopUp;
}

@media #{$medium-down} {
  .wrapper {
    .card-heading {
      margin-left: -16px;
      margin-right: -16px;
    }
  }
}
